@import url(https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
/* main container -------------------------------------------------*/
.container {
    /* position: relative; */
    min-width: 100vw;
    min-height: 100vh;
    transition: 0.5s;
    background-color: #364B72;
}
/* main container blur*/
.container#blur.active {
    -webkit-filter: blur(20px);
            filter: blur(20px);
    pointer-events: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
/* main container content */
.container .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
/* main header */
#nameHeader {
    font-weight: 600;
    font-size: 6em;
    padding-top: 200px;
    margin-left: 90px;
    display: flex;
    justify-content: center;
    color: plum;
}
#sub {
    display: flex;
    justify-content: center;

    color: lavender;
    font-size: 2.5em;
    padding: 20px;
}
.mainButton-row {
    display: flex;
    justify-content: space-evenly;
    margin-top: 30px;
}

.btn-space {
    margin-right: 20px;
    margin-left:20px
}
.mainButtons {
    align-self: center;
    text-align: center;
    color: plum;
    font-size: x-large;
    background: transparent;
    border: solid 3px paleturquoise;
    width: 200px;
    border-radius: 4px;
    padding: 10px;
}
.mainButtons:hover {
    background-color: plum;
    color: paleturquoise;
}

/*Button links to git linkden etc..  --------------------------------*/
.socialButton-row {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-bottom: 15px;
    position: absolute;
    right: 0;
    bottom: 0;
}

#gitButton {
    background-image: url(data:image/png;base64,);
    background-color: transparent;
    border: inset 3px  paleturquoise;
    height: 44px;
    width: 44px;
    cursor: pointer; 
    margin-right: 20px;   
}

#gitButton:hover {
    background-color: plum;
}
#linkedinButton {
    background-image: url(data:image/png;base64,);
    background-color: transparent;
    border:  inset 3px paleturquoise;
    height: 44px;
    width: 44px;
    cursor: pointer;
    margin-right: 15px;
}
#linkedinButton:hover {
    background-color: plum;
}

/* buttons for info -> contact */
.close-button {
    position: relative;
    padding: 5px 20px;
    display: inline-block;
    margin-top: 10px;
    text-align: center;
    text-decoration: none;
    color: plum;
    font-weight: bolder;
    background: #364B72;
    border: solid 1px paleturquoise;
    border-radius: 4px;
}
/* button effects -----------------------------------------------------*/
.close-button:hover {
    background-color: plum;
    color: paleturquoise;
    font-weight: 500;
}

/* POPUPS ----------------------------------------------------*/

/*info popup */
#popup {
    width: 600px;
    padding: 50px;
    box-shadow: 0 5px 30px rgba(0, 0, 0, .30);
    background: lightblue;
    border: outset 4px paleturquoise;
}
#popupHeader {
    border-bottom: 1px solid plum;
    margin-bottom: 25px;
    color: plum;
    font-size: x-large;
    font-weight: 800;
    font-style: italic;
}

#infoPara {
    margin-top: 15px;
    text-align: center;
    border: paleturquoise solid 1px;
    background: lightblue;
    color: #364B72;
}
.infoImage {
    display: flex;
    align-items: center;
    justify-content: center;
}

/* project popup */
#projects {
     width: 600px;
    padding: 50px;
    box-shadow: 0 5px 30px rgba(0, 0, 0, .30);
    background: lightblue;
    border: outset 4px paleturquoise;
    text-align: center;
}

/* resume popup */
#resume {
    width: 600px;
    padding: 50px;
    box-shadow: 0 5px 30px rgba(0, 0, 0, .30);
    background: lightblue;
    border: outset 4px paleturquoise;
    text-align: center;
}

/* accordion --------------------------------------------- */

.accordionHeader {
    color: plum !important;
    background: #364B72 !important;
    border: solid 4px paleturquoise !important;
    font-size: large !important;
    font-weight: 700 !important;
}
.accordionBody {
    color: #364B72 !important;
    background: lightblue !important;
    border:  solid 1px plum !important;
}

